// Variables

$color1: #CFCDFF;
$color2: #242527;
$tile-bgcolor: #0514F2;
$tile-p-bgcolor: #AED1FF;
$shadow-color: #000000;
$font-stack: Arial, Helvetica, sans-serif;

* { margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

#container {
  font: 100% $font-stack;
  width: 100%;
  position: absolute;
  left: 0;
}

#spy {
  position: relative;
  height: 700px;
  overflow-y:scroll;
}

header {
  background-color: $color1;					
  color: $color2;

  h1 {
    margin-top: 30px;
    margin-bottom: 20px;
    font-weight: bold;
  }
}

nav {
  background-color: $color1;
}

.nav-link {
  padding-right:20px;
  color: $color2
}

.nav-link:hover {
  font-weight:bold;
}

#bio { 
  background-color: $color2;					
  color: $color1;
}

.bold {
  font-weight: bold;
}

#footer {
  background-color: $color2;					
  color: $color1;
}

#profile-link img {
  width: 30px;
}

#projects {
  background-color: $color1;
  color: $color2;
}

.project-tile {
  background-color: $tile-bgcolor;
  box-shadow: 2px 5px 8px 2px $shadow-color;
  width: 300px;
  height: 300px;
  
  div {
    display: flex;
    align-items: center;
    align-content: center;
    height: 100%;
  }
  
  p {
    color: $color2;
    padding: 2px 0;
    background-color: $tile-p-bgcolor;
    border-bottom: 1px solid $color2;
  }

  :hover {
    box-shadow: 12px 16px 8px 2px $shadow-color;
  }
}

.project-image {
  max-width:80%;
}